@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

:root {
  --spotlight-height: 30em;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  margin: 0;
  padding: 0;
}

button {
  background: none;
  padding: 1em;
  border: 1px solid black;
  border-radius: 0.5em;
  margin: auto;
  display: block;
  cursor: pointer;
}

header {
  font-size: 1em;
  line-height: 2em;
  background: black;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

header h1 {
  margin: 0;
  padding: 0.5em;
  text-transform: uppercase;
}

header h1 a {
  color: white;
  font-style: normal;
}

header button {
  color: white;
  border: 1px solid white;
  margin: 1em;
}

footer {
  margin-top: 1em;
  padding: 1em;
  text-align: center;
  border-top: 1px solid black;
}

footer a {
  color: red;
}

a {
  text-decoration: none;
  color: black;
  font-style: italic;
}

.spotlight {
  height: 10em;
  height: var(--spotlight-height, 10em);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  margin-bottom: 5em;
}

.spotlight > * {
  color: white;
}

.container {
  width: 85%;
  margin: 1em auto;
}

.gallery {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1em;
}
@media only screen and (min-width: 768px) {
  .gallery {
    grid-template-columns: 1fr 1fr;
  }

  .spotlight {
    --spotlight-height: 42em;
  }

  .spotlight h2 {
    font-size: 5em;
  }
}

@media only screen and (min-width: 942px) {
  .gallery {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.gallery__item {
  text-align: center;
  background: #e8e8e8;
  border-radius: 0.5em;
  margin-bottom: 1em;
  padding: 1em 0;
}

.gallery__item_detail {
  border: 1px solid black;
  border-radius: 0.3em;
  padding: 0.2em 0.5em;
  font-style: normal;
  margin-left: 1em;
  display: inline-block;
}

.gallery__item img {
  width: 90%;
  height: auto;
  overflow: hidden;
}

button {
  transition: 250ms ease-in-out;
}

button:hover,
button:focus {
  background: black;
  color: white;
  transform: scale(1.1);
}

#cta {
  text-transform: uppercase;
  border: 1px solid white;
  border-radius: 0.5em;
  padding: 0.5em 1em;
}

#backToTop {
  position: fixed;
  bottom: 1em;
  right: 1em;
  background: rgba(255, 255, 255, 0.5);
}

.carousel .control-next.control-arrow:before {
  border-left: 8px solid black !important;
}

.carousel .control-prev.control-arrow:before {
  border-right: 8px solid black !important;
}

@media only screen and (min-width: 768px) {
  .carousel {
    margin: 0 auto !important;
    max-width: 50%;
    height: auto;
  }
}

form {
  display: flex;
  flex-direction: column;
}

.required::after {
  content: "*";
  color: red;
  display: inline;
}

.hint {
  color: rgba(0, 0, 0, 0.5);
}

table {
  width: 100%;
  text-align: left;
}

thead {
  color: rgba(0, 0, 0, 0.5);
}

tbody th {
  font-weight: normal;
}

table button {
  margin: 0;
  padding: 0.5em;
}

table button:hover {
  transform: none;
}

.single-item {
  text-align: center;
}

form {
  border-bottom: 1px solid black;
}

form input {
  margin-bottom: 1em;
}

.App {
  margin: 0;
  height: 100vh;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
}

header,
footer {
  flex: none;
}

main {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  flex: auto;
}

footer > * {
  display: inline-block;
  margin: 0;
}
